*, :after, :before {
  box-sizing: border-box;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.container {
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.chat {
  width: 320px;
  height: 533px;
  padding: 8px;
}

.chat__header {
  justify-content: space-between;
  margin-bottom: 7px;
  display: flex;
}

.chat__settings-button, .chat__exit-button {
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #000;
  padding: 7px 8px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.chat__inner {
  height: 440px;
  border: 1px solid #000;
  flex-direction: column-reverse;
  margin-bottom: 8px;
  padding: 8px;
  display: flex;
  overflow-y: auto;
}

.chat__inner::-webkit-scrollbar {
  width: 0;
}

.my-message {
  text-align: right;
  background-color: #f7f7f7;
  align-self: end;
}

.partners-message {
  background-color: #e5e5e5;
  align-self: start;
}

.my-message, .partners-message {
  max-width: 288px;
  margin-bottom: 8px;
  padding: 8px;
}

.my-message__time, .partners-message__time {
  font-size: 14px;
  font-style: italic;
  font-weight: 300;
  line-height: 16px;
}

.partners-message__time {
  text-align: right;
}

.my-message__text, .partners-message__text {
  margin-bottom: 4px;
}

.chat-form {
  justify-content: space-between;
  display: flex;
}

.chat-form__input {
  border: 1px solid #000;
  flex-grow: 1;
  margin-right: 8px;
  padding: 7px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.chat-form__input::placeholder {
  color: #7c7c7c;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.chat-form__button {
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #000;
  padding: 7px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.modal-overlay {
  height: 100vh;
  width: 100vw;
  background-color: #545353cc;
  justify-content: center;
  align-items: center;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.modal-overlay--open {
  display: flex;
}

.modal {
  width: 273px;
  height: 319px;
  background-color: #fff;
  padding: 16px 14px;
}

.modal__top {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 70px;
  display: flex;
}

.modal__close {
  width: 10px;
  height: 11px;
  cursor: pointer;
  background-color: #0000;
  background-image: url("close-icon.352727d0.svg");
  background-repeat: no-repeat;
  border: none;
}

.modal__text {
  margin-bottom: 8px;
}

.confirm-form, .auth-form {
  flex-wrap: wrap;
}

.confirm-form__input, .auth-form__input {
  margin: 0 0 7px;
}

